function setIframe(iframeRef, src) {
  console.log("setIframe", iframeRef, src);
  iframeRef.contentWindow["DevPortal"] = devPortalBridge;
  iframeRef.src = src;
  iframeRef.addEventListener("load", function() {
    // only access when loaded
    var win = iframeRef.contentWindow; // get reference to iframe's `window`
    win["foo"] = "bar"; // set global (within iframe)
  });
}

